:root {
  --md-sys-color-primary: #2196f3;
  --md-sys-color-secondary: #ff4081;
  --background: #f0f0f0;
  --font: #000;
}

body {
  background: var(--background);
  color: #606060;
  margin: 0;
  padding: 0 24px;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 14px;
}

header, main {
  max-width: 1140px;
  margin: 24px auto 0;
}

main {
  flex-direction: row;
  gap: 2rem;
  display: flex;
}

header {
  background-color: #e8e8e8;
  border-radius: 32px;
  height: 64px;
  position: relative;
  overflow: hidden;
}

button {
  all: unset;
  background-color: var(--md-sys-color-primary);
  color: #fff;
  cursor: pointer;
  padding: 0 12px;
}

#submit {
  position: inherit;
  float: right;
  z-index: 2;
  border-radius: 16px;
  height: 32px;
  margin: 16px;
}

#submit:disabled {
  cursor: not-allowed;
  background-color: #606060;
}

#github-link {
  position: inherit;
  z-index: 2;
  float: left;
  background-image: url("github-brands-solid-full.a1c4b5e6.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 32px;
  height: 32px;
  margin: 16px;
  display: block;
}

#error {
  text-align: center;
  width: 100%;
  color: var(--md-sys-color-secondary);
  margin: 0 auto;
  font-size: 16px;
  line-height: 64px;
  position: absolute;
  left: 0;
}

#progress {
  z-index: 1;
  width: 0%;
  height: inherit;
  background-color: var(--md-sys-color-primary);
  opacity: .25;
  transition: width .1s;
  position: absolute;
  top: 0;
  left: 0;
}

#main-data {
  flex-shrink: 0;
  width: 256px;
}

label {
  width: 100%;
  margin-bottom: 16px;
  display: block;
}

label div {
  color: #606060;
  padding: 0 0 4px;
  font-size: 14px;
}

input, textarea {
  all: unset;
  box-sizing: border-box;
  text-indent: 8px;
  background-color: #fff;
  border-radius: 8px;
  width: 100%;
  font-size: 16px;
  line-height: 24px;
}

input {
  height: 24px;
  line-height: 32px;
}

textarea {
  height: 48px;
}

.file-picker {
  text-align: center;
  color: #606060;
  cursor: pointer;
  box-sizing: border-box;
  border: 1px solid #606060;
  border-radius: 16px;
  width: auto;
  height: 32px;
  padding: 0 16px;
  line-height: 32px;
  display: inline-block;
}

.file-picker input {
  display: none;
}

#zone-data {
  flex: 1;
}

table {
  border-collapse: collapse;
  width: 100%;
  font-size: 14px;
}

td, th {
  text-align: left;
  border-bottom: 1px solid #ccc;
  padding: 8px;
}

#zone-data th:nth-child(2), #zone-data th:nth-child(3), #zone-data td:nth-child(2), #zone-data td:nth-child(3) {
  text-align: right;
}
